import React from "react";
import {OraSimpleRadio, OraButton, OraLoader} from "eshop/components/OraCommonComponents";
import {Expander, Trigger, Section} from "eshop/modules/core/components/ui/Expander";
import MulticartConsentValidationComponent from "eshop/modules/checkout/components/MulticartConsentValidationComponent";
import FactoryType from "eshop/modules/core/constants/FactoryTypeEnum";
export default class MulticartSingleConsentRadioButtonRow extends React.Component {
    constructor(props) {
        super(props);
    }

    getPropsForRadio(state, bundleAssignment) {
        const nameSuffix = !!bundleAssignment ? ("_" + bundleAssignment.bundleNo) : "";
        let checked = false;
        if(!!this.props.state && this.props.state.length !== 0) {
            if(!!bundleAssignment) {
                let bundleState = this.props.state.find(s => s.bundleNo === bundleAssignment.bundleNo);
                checked = !!bundleState && (bundleState.stateCode == state.code);
            } else {
                checked = this.props.state.find(a => a).stateCode == state.code;
            }
        }
        return {
            name: this.props.consent.consentCode + nameSuffix,
            text: state.description,
            value: state.code,
            labelClassName: "o-radio opl-radio",
            //readOnly: (this.props.readOnly || (this.props.consentsWithBonusLoading && this.props.isUpdating)),
            readOnly: this.props.consent.readonly,
            checked: checked,
            onChange: this.props.onStateChange.bind(this, bundleAssignment)
        };
    }

    endsWith(str, suffix) {
        return str.indexOf(suffix, str.length - suffix.length) !== -1;
    }

    getHeader() {
        return (
            <legend className="l-col l-col-8 l-col-small-12 l-col-medium-12">
                <span className="u-font-bold u-block">{this.props.consent.title}</span>
                <span className="u-block u-padding-top-l u-medium-padding-l u-small-padding-l" dangerouslySetInnerHTML={{ __html: this.props.consent.description}}/>
            </legend>
        );
    }
    formatMsisdn (msisdn, factoryType) {
        if (!!msisdn && msisdn.length === 9) {
            switch (factoryType) {
                case FactoryType.FIX:
                    return msisdn.substring(0, 2) + " " + msisdn.substring(2, 5) + " " + msisdn.substring(5, 7) + " " + msisdn.substring(7, msisdn.length);
                default:
                    return msisdn.substring(0, 3) + ' ' + msisdn.substring(3, 6) + ' ' + msisdn.substring(6, msisdn.length);
            }
        } else {
            return msisdn;
        }
    }

    getTitle(){
        return this.props.consent.title;
    }

    getRadioRow(consentPositiveState, consentNegativeState, ba, businessImplicationsElement, index, lastRow) {
        return (
            <div className={(index==0 && this.props.isDeliveryStep)?"l-row":"l-row u-border-top"}>
                <div className={((lastRow ? "u-small-padding-l u-medium-padding-l " : "u-padding-l ") + "l-col l-col-small-12 l-col-medium-12 l-col-8 u-padding-top-l " + (index === 0 ? "u-small-no-padding-t u-medium-no-padding-t" : ""))}>
                    <span className="u-spacing-right" dangerouslySetInnerHTML={{__html: ba.label}}/>
                    {businessImplicationsElement}
                </div>
                <div className={((lastRow ? "u-small-padding-l " : "u-padding-l ") + "l-col l-col-small-12 l-col-medium-4 l-col-2 u-padding-top-l u-medium-no-padding-t u-small-no-padding-t")}>
                    {consentPositiveState && <OraSimpleRadio {...this.getPropsForRadio(consentPositiveState, ba)}/>}
                </div>
                <div className={((lastRow ? "" : "u-padding-l ") + "l-col l-col-small-12 l-col-medium-4 l-col-2 u-padding-top-l u-medium-no-padding-t u-small-no-padding-t")}>
                    {consentNegativeState && <OraSimpleRadio {...this.getPropsForRadio(consentNegativeState, ba)}/>}
                </div>
                <div id={"consent-error-message" + index} className="l-col l-col-small-12 l-col-medium-12 l-col-8">
                    <MulticartConsentValidationComponent
                        className={(lastRow) ? "u-padding-top-l" : "u-padding-l"}
                        scrollOffsetCalculateFrom={"consent-error-message" + index}
                        scrollOffsetCalculateTo={"consent-section"}
                        consentsErrorMsg={this.props.consent.wariningMessage} {...this.props}
                        correspondingBa={ba}/>
                </div>
            </div>
        );
    }

    getMsisdnRadioRow(consentPositiveState, consentNegativeState, ba, businessImplicationsElement) {
        return (
            <fieldset className="l-row u-margin-l u-large-margin">
                <legend className="l-col l-col-8 l-col-small-12 l-col-medium-4 u-font-bold">{ba.label}</legend>
                <div className="l-col l-col-small-6 l-col-medium-4 l-col-2  u-large-text-right u-small-padding-top">
                    {consentPositiveState && <OraSimpleRadio {...this.getPropsForRadio(consentPositiveState, ba)}/>}
                </div>
                <div className="l-col l-col-small-6 l-col-medium-4 l-col-2  u-large-text-right u-small-padding-top">
                    {consentNegativeState && <OraSimpleRadio {...this.getPropsForRadio(consentNegativeState, ba)}/>}
                </div>
            </fieldset>
        );
    }

    renderMsisdnMatrix(consentPositiveState, consentNegativeState){
        return (
            this.props.consent.msisdns && this.props.consent.msisdns.length > 0 ? (
                <div className="u-padding-top-m">
                    {this.props.consent.msisdns.map(entry => {
                        return this.getMsisdnRadioRow(consentPositiveState, consentNegativeState, {bundleNo: entry.bundleNo, label: "numer telefonu "+this.formatMsisdn(entry.msisdn, entry.factoryType)});
                    })
                    }
                </div>
            ) : ""
        );
    }

    getRadioRows(consentPositiveState, consentNegativeState, singleRadio) {
        if(!singleRadio) {
            const bundleAssignmentsLastIndex = this.props.consent.bundleAssignments.length -1;
            return (
                <div className="l-col l-col-12">
                    {this.props.consent.bundleAssignments.map((ba, index) => {
                        let state = "";
                        const lastRow = (index === bundleAssignmentsLastIndex);
                        if(!!this.props.state && this.props.state.length > 0) {
                            state = this.props.state.find(s => s.bundleNo === ba.bundleNo) || "";
                        }
                        const businessImplications = this.props.consent.states
                                    .filter(st => st.code === state.stateCode)
                                    .map(s => s.businessImplicationsDescr)
                                    .find(bi => bi);
                        const businessImplicationsElement = (!!businessImplications) ?
                                    <div className="u-padding-m u-padding-top-m o-icon-text g-icon g-icon--info g-icon--before g-icon--xs-s g-blue2-c">
                                             <p className="o-icon-text__text g-black1-c">{businessImplications}</p>
                                    </div> :
                                    null;
                        return this.getRadioRow(consentPositiveState, consentNegativeState, ba, businessImplicationsElement, index, lastRow);
                    })}
                </div>
            );
        } else {
            if(!this.props.consent.msisdns || this.props.consent.msisdns.length === 0) {
                return (
                    <div>
                        <div>
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-2  u-large-text-right u-small-padding-l">
                                {consentPositiveState && <OraSimpleRadio {...this.getPropsForRadio(consentPositiveState)}/>}
                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-2  u-large-text-right u-small-padding-l">
                                {consentNegativeState && <OraSimpleRadio {...this.getPropsForRadio(consentNegativeState)}/>}
                            </div>
                        </div>
                        <div id="single-error-message" className="l-col l-col-small-12 l-col-medium-12 l-col-8">
                            <MulticartConsentValidationComponent
                                className="u-large-padding-top-l u-medium-padding-top-l"
                                scrollOffsetCalculateFrom={"single-error-message"}
                                scrollOffsetCalculateTo={"consent-section"}
                                consentsErrorMsg={this.props.consent.wariningMessage} {...this.props}/>
                        </div>
                    </div>
                );
            }
        }
    }

    getViewExpander(consentPositiveState, consentNegativeState) {

        //TODO: Tymczasowe usunięcie komunikatu na stronie dostawa i płatność dla fix do czasu rozdzielenia komunikatów.
        const NotFixOnDiP = !(this.props.factoryType == "FIX" && !!this.props.selectedMethod);

        const condition = !!this.props.state && !!this.props.consent.states && NotFixOnDiP;
        const singleState = (!!this.props.state && this.props.state.length > 0) ? this.props.state.find(st => st) : "";
        const singleRadio = !this.props.consent.bundleAssignments || this.props.consent.bundleAssignments.length === 0;
        const singleBusinessImplications = condition ? this.props.consent.states.filter(state => state.code === singleState.stateCode).map(s => s.businessImplicationsDescr).find(d=>d) : "";
        const colStyle=!!this.props.consent.msisdns?"l-col-12":"l-col-8";
        if(!this.props.consent.msisdns || this.props.consent.msisdns.length === 0) {
            return (
                <fieldset className="l-row" ref={(ref) => {this.ref = ref}}>
                         {this.getHeader()}
                         {this.getRadioRows(consentPositiveState, consentNegativeState, singleRadio)}
                 </fieldset>
            );
        } else {
            return (
                <p className="u-font-bold">{this.props.consent.title}</p>
            );
        }
    }

    render() {
        let consentPositiveState = this.props.consent.states.find((state) => state.positive);
        let consentNegativeState = this.props.consent.states.find((state) => !state.positive);
        return(
            <div id="consent-section">
                {this.getViewExpander(consentPositiveState, consentNegativeState)}
                {this.props.consent.msisdns && this.props.consent.msisdns.length !== 0 &&
                    <span className="u-block u-padding-top-l u-medium-padding-l u-small-padding-l" dangerouslySetInnerHTML={{ __html: this.props.consent.description}}/>
                }
                {this.renderMsisdnMatrix(consentPositiveState, consentNegativeState)}
                {this.getBusinessImplications()}
            </div>
        );
    }

    getBusinessImplications(){
        const NotFixOnDiP = !(this.props.factoryType == "FIX" && !!this.props.selectedMethod);

        const condition = !!this.props.state && !!this.props.consent.states && NotFixOnDiP;
        const singleState = (!!this.props.state && this.props.state.length > 0) ? this.props.state.find(st => st) : "";
        const singleRadio = !this.props.consent.bundleAssignments || this.props.consent.bundleAssignments.length === 0;
        let singleBusinessImplicationsState = condition ? this.props.consent.states.filter(state => state.code === singleState.stateCode).map(s => s.businessImplicationsDescr).find(d=>d) : "";

        let singleBusinessImplications = (this.props.consent.isRelatedWithBonus && (this.props.state.length === 0 || !singleState.stateCode)
                            ? this.props.descriptions.bonusSingleBusinessImplications : singleBusinessImplicationsState);

        let singleBusinessImplicationsWarning = this.props.consent.wariningMessage ? this.props.consent.wariningMessage : singleBusinessImplications;

        const finalCondition = (singleRadio && !!singleBusinessImplicationsState)
                            || singleBusinessImplicationsWarning && (this.props.state.length === 0 || !singleState.stateCode);


        return (finalCondition && !this._hasError() &&
                  <div className="u-padding-m u-padding-top-m o-icon-text g-icon g-icon--info g-icon--before g-icon--xs-s g-blue2-c">
                    <p className="o-icon-text__text g-black1-c">{((this.props.state.length === 0 || !singleState.stateCode) && singleBusinessImplicationsWarning)
                     || singleBusinessImplicationsState}</p>
                 </div>);
    }

    _hasError() {
        const errorElem = this.props.consentsErrorList;
        return (errorElem) && this.props.isMandatory && (!this.props.state || this.props.state.length==0 || !this.props.state[0].stateCode);
    }
}
